<template>
    <div class="traffic">
        <!--  -->
        <div class="header row">
            <div class="cell cell-index">#</div>
            <div class="cell cell-status">Status</div>
            <div class="cell cell-method">Method</div>
            <div class="cell cell-protocol">Protocol</div>
            <div class="cell cell-host">Host</div>
            <div class="cell cell-path">Path</div>
            <div class="cell cell-type">Type</div>
            <div class="cell cell-time">Time</div>
        </div>
        <!--   -->
        <list :total="$dc.total"
              :height="height - 28"
              :rowHeight="24">
            <template scope="props">
                <record v-for="index in props.ids" :index="index" :id="$dc.filterdRecordArray[index]" :key="index"
                        @right-clicked="rightClicked"></record>
            </template>
        </list>
        <context-menu id="testingctx" ref="ctx"
                      :ctxOpen="onCtxOpen"
                      :ctxCancel="resetCtxLocals"
                      :ctxClose="onCtxClose">
            <li class="ctx-item" @click="saveData">mock</li>
            <li class="ctx-item" @click="copyUrl">url</li>
        </context-menu>
    </div>
</template>
<script>
    import _ from 'lodash';
    import List from './List.vue';
    import copyToClipboard from 'copy-to-clipboard';
    import ContextMenu from '../../context-menu';
    import dataApi from '../../api/data';
    import './httptraffic.pcss';
    import Record from './record.vue';
    export default {
        props: ['height'],
        components: { List, ContextMenu, Record },
        data(){
            return {};
        },
        methods: {
            // -------------------------------
            saveData(){
                if (!this.$dc.rightClickRow.resTime) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                    return;
                }
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: ''
                }).then(({ value }) => {
                    dataApi.saveDataEntryFromTraffic(this.$dc.rightClickRow.idx, value
                        , this.$dc.rightClickRow.contentType.split(';')[0]).then((res) => {
                        var serverData = res.data;
                        if (serverData.code == 0) {
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                        } else {
                            this.$message.error(`${serverData.msg}`);
                        }
                    });
                });
            },
            // url
            copyUrl(){
                copyToClipboard(`${this.$dc.rightClickRow.protocol}://${this.$dc.rightClickRow.host}${this.$dc.rightClickRow.path}`);
                this.$message('url');
            },

            // -------------------------------
            // 
            onCtxOpen(recordId) {
                this.$dc.setRightClickedRecordId(recordId);
            },
            rightClicked(event,recordId){
                this.$refs.ctx.open(event, recordId)
            },
            // 
            onCtxClose(locals) {
            },
            // 
            resetCtxLocals() {
                this.$dc.setRightClickedRecordId('');
            }
        }
    };
</script>


